<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div v-if="productsCount == 0">
      <router-link to="/customer_order" class="btn btn-outline-primary gopaid mb-2" style="font-size:5px">
        <i class="far fa-arrow-alt-circle-left fa-xs mr-1"></i>回到商店街
      </router-link>
    </div>
    <div v-if="productsCount > 0" class="table-responsive">
      <table class="table">
        <thead class="thead-light">
          <tr>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col" class="pl-0">商品</th>
            <th scope="col" class="pl-0">價格</th>
            <th scope="col" class="pl-0">數量</th>
            <th scope="col" class="pl-0">小計</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in productsInCart.carts" :key="index">
            <td class="text-center px-0">
              <a
                href="#"
                class="btn btn-outline-primary btn-outline-customize"
                @click.prevent="deleteOneProductInCart(item.id)"
              >
                <i class="fas fa-times"></i>
              </a>
            </td>
            <td class="p-0">
              <img
                :src="item.product.imageUrl"
                style=" max-width:50px;display: block;
                      margin-left: auto;
                      margin-right: auto;"
              />
            </td>
            <td class="text-left px-0 pt-4">
              {{item.product.title}}
              <span class="ml-1 text-success" v-if="item.coupon">已套用優惠券</span>
            </td>
            <td class="text-left px-0 pt-4 ntDollar">NT${{item.product.price}}</td>
            <td class="text-left px-0 pt-4">{{item.qty}}</td>
            <td class="text-left px-0 pt-4 ntDollar">NT${{item.total}}</td>
          </tr>
        </tbody>
        <tfoot>
          <tr>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td class="text-right">
              <input v-model="couponCode" type="text" name="code" id="coupon" placeholder="折價券">
            </td>
            <td>
              <button class="btn btn-outline-primary w-100 gopaid" style="font-size:5px"
              @click="useCoupon">
                使用折價券
              </button>
            </td>
          </tr>
        </tfoot>
      </table>
    </div>
    <div v-if="productsCount > 0" class="row justify-content-end">
      <div class="col-6">
        <div class="table-responsive">
          <table class="table">
            <thead class="thead-light">
              <tr>
                <th class="text-center" colspan="2">購物車總計</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>小計</td>
                <td class="ntDollar">NT$ {{productsInCart.total}}</td>
              </tr>
              <tr>
                <td>運送方式</td>
                <td>宅配</td>
              </tr>
              <tr>
                <td>總計</td>
                <td class="ntDollar">
                  NT$ {{productsInCart.final_total}}
                  <span class="ml-1 text-success" v-if="productsInCart.total != productsInCart.final_total">
                    優惠價
                  </span>
                </td>
              </tr>
            </tbody>
            <tfoot>
              <tr>
                <td colspan="2">
                  <router-link to="/customer_order/formList" class="btn btn-outline-primary w-100 gopaid" style="font-size:5px">
                    前往結帳
                  </router-link>
                </td>
              </tr>
            </tfoot>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      productsInCart: {},
      productsCount: 0,
      isLoading: false,
      couponCode: '',
    };
  },

  created() {
    console.log("ClientOrderList created");
    this.getProductCart();
  },
  methods: {
    getProductCart() {
      console.log("begin to getProductCart");
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_USER}/cart`;
      console.log(api);
      var vm = this;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        console.log("response data:");
        console.log(response);
        if (response.data.success) {
          console.log(response.data);
          vm.productsInCart = Object.assign({}, response.data.data);
          vm.productsCount = vm.productsInCart.carts.length;
        } else {
          console.log("get product cart failed");
          console.log(response.data.message);
        }
        vm.isLoading = false;
      });
    },

    deleteOneProductInCart(id) {
      console.log("begin to deleteOneProductInCart");
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_USER}/cart/${id}`;
      console.log(api);
      var vm = this;
      vm.isLoading = true;
      this.$http.delete(api).then(response => {
        console.log("response data:");
        console.log(response);
        if (response.data.success) {
          this.getProductCart();
          vm.$bus.$emit("message:refreshCart");
        } else {
          console.log("delete product in cart failed");
          console.log(response.data.message);
        }
        vm.isLoading = false;
      });
    },
    useCoupon() {
      console.log("Use coupon");
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_USER}/coupon`;
      console.log(api);
      var vm = this;
      vm.isLoading = true;
      console.log("coupon code " + this.couponCode)
      this.$http.post(api,{data:{'code':this.couponCode}}).then(response => {
        console.log("response data:");
        console.log(response);
        if (response.data.success) {
          console.log("success to use coupon")
          console.log(response.data.data.final_total);
          this.getProductCart();
        } else {
          vm.$bus.$emit('message:push',response.data.message)
        }
        vm.isLoading = false;
      });
    }
  }
};
</script>


